* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background: #F5F5F5 !important;
  display: flex;
  flex-direction: row;
  justify-content: center;
  min-height: 100%;
}

.container {
  flex-direction: column;
  justify-content: space-between;
  flex-grow: 1;
  width: 100%;
  max-width: 1226px;
}

#root {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100%;
  align-items: center;
  /* background-color: rgba(0, 0, 0, 0); */
}

.header {

  display: flex;

  justify-content: space-between;
  align-self: stretch;
  align-items: center;
  padding: 10px 20px;
  width: 100%;
  max-width: 1226px;
}

.headWrapper {
  position: sticky;
  top: 0;
  background-color: #fff;
  margin-bottom: 15px;
  width: 100%;
  box-shadow: 0 0 10px -5px;
  display: flex;
  justify-content: center;
}

.mainRow {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  margin-top: 20px;
}

.outer {
  /* border: 1px solid; */
  width: 80%;
}

.homeLayout {
  display: flex;
  
}

.postPart {
  flex-grow: 1;
}

/* .rightPart {
  width: 270px;
} */

.box {
  display: flex;
  box-shadow: 0 0 10px -5px gray;
  border-radius: 5px;
  padding: 10px;
  background-color: #fff;
  margin-bottom: 10px;
  white-space: pre-wrap;
  word-break: break-all;
  font-size: 18px;
}

.titleLink {
  white-space: normal;
}

a {
  text-decoration: none;
  height: fit-content;
  width: fit-content;
}

.box a:hover {
  text-decoration: underline;
}

.banner {
  width: 100%;
  height: 0;
  font-size: 0;
  line-height: 0;
  padding: 15% 0 0;
  background: linear-gradient(-45deg,#ff6b81, #eccc68);
}

.changeCaptcha {
  cursor: pointer;
  color: skyblue;
}

.avatarInBox {
  width: 50px;
  height: 50px;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 5px;
  box-shadow: 0 0 8px -3px black;
  align-self: flex-start;
}

.delete {
  cursor: pointer;
}

.delete:hover {
  text-decoration: underline;
}

.personPageAvatar {
  width: 80px;
  height: 80px;
  align-self: center;
}

@media (max-width: 600px) {
  .banner {
    display: none;
  }
  .outer {
    width: 100%;
  }
  .mainRow {
    margin-top: 0;
  }
  .box {
    border-radius: 0;
    font-size: 15px;
  }
  .avatarInBox {
    width: 40px;
    height: 40px;
  }
  .personPageAvatar {
    width: 60px;
    height: 60px;
  }
}

.shade {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 9999;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.shadeBox {
  position: relative;
  padding: 30px 80px;
  background-color: #F5F5F5;
  text-align: center;
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-shadow: 0px 0px 50px -10px black;
}

.closeBox {
  cursor: pointer;
  position: absolute;
  width: 15px;
  height: 15px;
  line-height: 15px;
  right: 0;
  top: 0;
}

.welcomeInfo {
  display: flex;
  align-items: center;
}

.welcomeInfo a {
  flex: 1;
  display: flex;
  justify-content: center;
}

.avatar {
  width: 20px;
  height: 20px;
  background-size: cover;
}

.fail {
  color: red;
}

.success {
  color: skyblue;
}

.con {
  color: red;
}

.notCon {
  color: skyblue;
}

.textZone {
  margin-top : 20px;
}

.postMain {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 250px;
}

.postTitle {
  width: 100%;
  outline: none;
  border: none;
  box-shadow: 0 0 10px -5px;
  padding: 0 10px;
}

.postContent {
  width: 100%;
  outline: none;
  flex-grow: 1;
  border: none;
  box-shadow: 0 0 10px -5px;
  padding: 0 10px;
}

.commentContent {
  width: 100%;
  outline: none;
  border: none;
  flex-grow: 1;
  box-shadow: 0 0 10px -5px;
  padding: 0 10px;
}

.footer {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;
  align-self: stretch;
  margin-top: 15px;
  padding-top: 5px;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  background-color: #fff;
  box-shadow: 0 0 6px -3px;
  /* line-height: 50px; */
  /* min-height: 80px; */
}

.postBtn {
  width: 30%;
  background-color: #fff;
  margin-top: 10px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  outline: none;
  height: 40px;
  box-shadow: 0 0 6px -3px;
  align-self: center;
}

.postBtn:active {
  box-shadow: inset 0 0 6px black;
}

.logoutBtn {
  margin: 0 0 0 5px;
  background-color: red;
  padding: 0 5px;
  border-radius: 5px;
  color: #fff;
  border: none;
  outline: none;
  cursor: pointer;
}

.contentInBox {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  padding-left: 13px;
}

.posterInfo {
  display: flex;
  margin-top: 5px;
  font-size: 0.83em;
}

.poster {
  color: rgba(0, 0, 0, 0.6) !important;
  font-weight: 800;
}

.separate {
  padding: 0 5px;
  color: rgba(0, 0, 0, 0.2);
}

.time {
  color:  rgba(0, 0, 0, 0.4);
}

.loginTip {
  text-align: center;
}

.inputStyle {
  outline: none;
  border: none;
  background-color: #fff;
  box-shadow: 0 0 5px -2px;
  padding: 5px;
}

.logRegBtn {
  border: none;
  background-color: #fff;
  padding: 10px 40px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-shadow: 0 0 6px -3px;
  outline: none;
}

.logRegBtn:active {
  box-shadow: inset 0 0 6px black;
}

.ant-notification {
  z-index: 999999 !important;
}

div.ant-notification-notice {
  width: 300px;
}

.motto {
  font-size: 15px;
  padding: 5px;
  color: rgba(0, 0, 0, 0.3);
}

.title {
  display: flex;
  flex-direction: column;
}

.titleContent {
  display: flex;
  padding-top: 10px;
}

.timeInTitle {
  font-size: 0.83em;
  color: rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: space-between;
}

.timeInSub {
  justify-content: flex-start;
}

.postPageDate {
  flex-direction: column;
}

.titleInline {
  padding-left: 13px;
  align-self: flex-start;
}

.tip {
  text-align: center;
}

.floor {
  width: fit-content;
  padding: 0 10px;
  color: rgba(0, 0, 0, 0.6);
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: 999px;
}

.thanks {
  font-size: 18px;
  padding: 5px;
}

hr {
  border-color: rgba(0, 0, 0, 0.1);
  margin: 5px 0;
}

.personComment {
  background-color: rgba(0, 0, 0, 0.1);
}